                                            <p class="repImteRight">{{isNone(item.physical.Pulse)?'':item.physical.Pulse+'/'}}</p>
                                        </div>
                                        <div class="repItem" v-show="!isNone(item.physical.breathing)">
                                            <p class="repImteLeft"></p>
                                            <p class="repImteRight">{{isNone(item.physical.breathing)?'':item.physical.breathing+'/'}}</p>
                                        </div>
                                        <div class="repItem" v-show="!isNone(item.physical.temperature)">
                                            <p class="repImteLeft"></p>
                                            <p class="repImteRight">{{isNone(item.physical.temperature)?'':item.physical.temperature}}</p>
                                        </div>
                                        <div class="repItem" v-show="!isNone(item.physical.bodySurfaceArea)">
                                            <p class="repImteLeft"></p>
                                            <p class="repImteRight">{{isNone(item.physical.bodySurfaceArea)?'':item.physical.bodySurfaceArea}}</p>
                                        </div>
                                        <div class="repItem" v-show="!isNone(item.physical.other)">
                                            <p class="repImteLeft"></p>
                                            <p class="repImteRight">{{isNone(item.physical.other)?'':item.physical.other}}</p>
                                        </div>
                                    </div>
                                    <div class="repItemTitle" v-show="!isNone(item.personalHistory)">
                                        <span></span>
                                    </div>
                                    <div class="repItemDesc" v-show="!isNone(item.personalHistory)">
                                        <div class="repItem">
                                            <p class="repImteLeft"></p>
                                            <p class="repImteRight">{{isNone(item.personalHistory)?'':item.personalHistory}}</p>
                                        </div>
                                    </div>
                                    <div class="repItemTitle" v-show="!isNone(item.familyHistory)">
                                        <span></span>
                                    </div>
                                    <div class="repItemDesc" v-show="!isNone(item.familyHistory)">
                                        <div class="repItem">
                                            <p class="repImteLeft"></p>
                                            <p class="repImteRight">{{isNone(item.familyHistory)?'':item.familyHistory}}</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </article>
            </section>
            <!---->
            <videoPlayer :path="path" :beginPlay="beginPlay" @beginFn="beginFn" :codeType="codeType"></videoPlayer>
            <!---->
        </section>
        <loading v-show="showLoad"></loading>
    </div>
</template>
<script>
    import NavSideBar from '~components/common/NavSideBar.vue';
    import TopHeader from '~components/common/caseDetailHeaderTopNav.vue';
    import caseDetail from './components/caseDetail.vue';
    import videoPlayer from  '~components/videoPlayer/videoPlayer.vue'
    import loading from '~components/loading/loading.vue';
    import comm from "~utils/comm.js";
    import user from "~utils/user.js";
    const $ = require('jquery');
    import {mapActions,mapGetters} from 'vuex';
    import viewBigImgAll from '../../utils/view-big-img/module.view-big-img.js';
    const URL={
        inqLeft:'/call/patient/case/getInquisitionList/',//
        inqRight:'/call/patient/case/getMapByCaseIds/',//
        isBlong:'/call/caseFolder/baseinfo/checkIsBelongTeam/',//
    };
    export default {
        name: 'index-app',
        data() {
            return {
                cId:comm.cookie.get("userId"),
                caseId:comm.getpara().caseId,
                reportLogNone:false,//
                reportLog:false,//
                repLogFlag:true,//
                inqLogNone:false,//
                inqLog:false,//
                inqLogFlag:true,//
                path:'',//
                beginPlay:false,//
                codeType:2,//
                inqLeft:[],//
                indexArr:[],//Tab
                inqIndex:0,//active
                inqRightArr:[],//
                inqRight:[],//
                repRight:[],//
                countDown:3,//
                ContH:0,//
                marH:0,//
                isHasReady:false,
                assistantDoctor:parseInt(comm.cookie.get("customerRole"))===13,
            }
        },
        components: {
            NavSideBar,
            TopHeader,
            caseDetail,
            loading,
            videoPlayer,
        },
        //
        computed:{
            ...mapGetters(['showLoad','baseMustInfo','patientId','isBlong'])
        },
        methods:{
            //
            ...mapActions(['showLoadi','baseMust','chaPatientId','chaIsBlong']),
            //
            isBlongFn(){
                let t = this;
                t.showLoadi(true);
                comm.ajax2({
                    url:URL.isBlong,
                    type: "get",
                    data: {
                        paramJson:JSON.stringify({
                            customerId:t.cId,
                            caseId:t.caseId,
                            visitSiteId:1,
                        })
                    },
                    timeout: 30000,
                    success:function(res){
                        t.showLoadi(false);
                        if(res&&res.responseObject&&res.responseObject.responseData&&res.responseObject.responseData&&res.responseObject.responseStatus){
                            t.isHasReady = true;
                            if(res.responseObject.responseData.isBelong==1){//
                                t.chaIsBlong(true);//
                            }else{
                                t.chaIsBlong(false);//
                                t.countDownFn();//
                                // window.location.href = '/';//
                            }
                        }
                    },
                })
            },
            //
            repRecordCli(){
                let t = this;
                // document.getElementsByTagName('body')[0].className = 'bodyHidden';
                // $('body').addClass('bodyHidden');
                $('body').css('position',"");
                $('body').css('overflow',"hidden");
                if(t.repLogFlag){
                    // t.reportLogNone = true;
                }else{
                    //
                    t.inqRightCon(2,0,t.caseId);//
                }
            },
            //
            InqRecordCli(){
                let t = this;
                document.getElementsByTagName('body')[0].className = 'bodyHidden';
                // $('body').addClass('bodyHidden');
                $('body').css('position',"");
                $('body').css('overflow',"hidden");
                if(t.inqLogFlag){
                    // t.inqLogNone = true;
                }else{//
                    t.inqLeftBar();//
                }
            },
            repInqQuit(){
                let t = this;
                // $('body').removeClass('bodyHidden');
                t.showLoadi(false);
                // document.getElementsByTagName('body')[0].classList.remove("bodyHidden");
                $('body').css('position',"static");
                $('body').css('overflow',"scroll");
                t.reportLogNone=false;//
                t.reportLog=false;//
                t.inqLogNone=false;//
                t.inqLog=false;
                t.indexArr = [];
                t.inqLeft = [];
                t.inqRight = [];
                t.repRight = [];
                t.inqRightArr = [];
                t.inqIndex  = 0;
            },
            inqLeftBar(flag){//,flag:
                let t = this;
                t.showLoadi(true);
                comm.ajax2({
                    url: URL.inqLeft,
                    type: "post",
                    data: {
                        paramJson:JSON.stringify({
                            patientId:t.patientId,
                            customerId:t.cId,
                            // patientId:"1524022739020",
                            // customerId:"1428327147313"
                        })
                    },
                    timeout: 30000,
                    success:function(res){
                        t.showLoadi(false);
                        if(res&&res.responseObject&&res.responseObject.responseData&&res.responseObject.responseData.dataList){
                            if(res.responseObject.responseData.dataList.length>0){
                                if(flag){//
                                    t.inqLogFlag = false;
                                }else{
                                    t.inqLeft = res.responseObject.responseData.dataList;
                                    t.inqRightCon(1,0,t.inqLeft[0].caseId);
                                }
                            }else{
                                t.inqLogFlag = true;
                            }
                        }else{
                            t.inqLogFlag = true;
                        }
                    },
                })
            },
            inqRightCon(type,index,caseId,flag){//
                let t = this;
                let params = '';
                t.showLoadi(true);
                if(type==1){
                    params = {
                        caseIdList:caseId,
                        visitSiteId:1,
                        caseSocure:type,//1--2--
                        patientId:t.patientId,
                        customerId:t.cId,
                        attUseFlag:'9,12',
                    };
                }else{
                    params = {
                        visitSiteId:1,
                        caseSocure:type,//1--2--
                        patientId:t.patientId,
                        customerId:t.cId,
                        attUseFlag:'9,12',
                    };
                }
                comm.ajax2({
                    url:URL.inqRight,
                    type: "get",
                    data: {paramJson:JSON.stringify(params)},
                    timeout: 30000,
                    success:function(res){
                        t.showLoadi(false);
                        if(flag){
                            if(res&&res.responseObject&&res.responseObject.responseData&&res.responseObject.responseData.dataList){
                                if(type==1){//
                                    t.inqLog = false;
                                }else{//
                                    t.repLogFlag = false;
                                }
                            }else{
                                if(type==1){
                                    t.inqLogFlag = true;
                                }else{
                                    t.repLogFlag = true;
                                }
                            }
                        }else{
                            if(res&&res.responseObject&&res.responseObject.responseData&&res.responseObject.responseData.dataList){
                                if(type==1){//
                                    t.inqLog = true;
                                    t.inqRight = res.responseObject.responseData.dataList;
                                    t.inqRightArr.push(t.inqRight);
                                    t.indexArr.push(index);
                                }else{//
                                    t.reportLog = true;
                                    t.repRight = res.responseObject.responseData.dataList;
                                }
                            }else{
                                if(type==1){
                                    // t.inqLogNone = true;
                                    t.inqLogFlag = true;
                                }else{
                                    // t.reportLogNone = true;
                                    t.repLogFlag = true;
                                }
                            }
                        }
                    },
                })
            },
            inqItemCli(index,caseId){//
                let t = this;
                t.inqIndex = index;
                if(t.indexArr.indexOf(index)>=0){//
                    t.inqRight = t.inqRightArr[index];
                }else{//
                    t.inqRightCon(1,index,caseId);
                }
            },
            viewBigImg(e){
                e.stopPropagation();
                e.preventDefault();
                var imgList=[],
                    bigImg=$(e.target).parents('.ev-bigImg'),
                    id=bigImg.attr("listId"),
                    container=$(e.target).parents("ul");
                if(id){
                    $('.ev-bigImg',container).each(function(i,em){
                        imgList.push({id:$(em).attr("listid"),url:$(em).find("img").attr("src")});
                    });
                    viewBigImgAll({
                        container:container,
                        typeTitle: "",
                        imgListType:'caze',
                        id: id,
                        imgList:imgList,
                        srcSplit:true
                    });
                }
            },
            videoCli(path,state){
                if(state==2){
                    this.path = path.replace('http:','');
                }
                this.codeType = state;
                this.beginPlay = true;
            },
            beginFn(flag){
                this.beginPlay = flag;
            },
            repTxt(type){//
                switch (type){
                    case 1:
                        return '';
                    case 2:
                        return '';
                    case 3:
                        return '';
                }
            },
            imgNum(num1,num2,num3){//0
                return parseInt(num1.length)+parseInt(num2.length)+parseInt(num3.length);
            },
            countDownFn(){
                let t=this;
                if(!t.isBlong){
                    let timer = setInterval(function () {
                        if(t.countDown<=0){
                            clearInterval(timer);
                            window.location.href = '/';
                        }else{
                            t.countDown--;
                        }
                    },1000);
                }
            },
            gainHeight(){//,
                let WH = window.innerHeight;
                this.ContH = WH - 150-80;//
                this.marH = (WH-600)/2;//
            },
            isNone(data){
                if(data==''||comm.isEmptyObject(data)){
                    return true;
                }else{
                    return false;
                }
            },
            caseTimeFilter(data,data2){//
                if(data){
                    let ymd = data.split(' ')[0];
                    let txt = '';
                    ymd = ymd.split('-')[0]+''+ymd.split('-')[1]+''+ymd.split('-')[2]+'';
                    if(data2&&data2!=0){
                        switch (data2){
                            case 1:
                                txt = '';
                                break;
                            case 2:
                                txt = '';
                                break;
                            case 3:
                                txt = '';
                                break;

                        }
                        return ymd+' '+txt;
                    }else{
                        return ymd;
                    }

                }
            },
            dataRes(typ,resType,data1,data2){
                if(typ==1){//
                    if(data1&&data1.length>0){
                        return false;
                    }else{
                        return true;
                    }
                }else{//
                    if(resType!=''){//id
                        switch (parseInt(resType)){
                            case 1://
                            case 2://
                                if(data2){
                                    if(data2.length>4){
                                        return data1+' - '+data2.substring(0,4)+'...';
                                    }else{
                                        return data1+' - '+data2;
                                    }
                                }else{
                                    return data1;
                                }
                                break;
                            case 0://
                                if(data1&&data1.length>0){
                                    if(data1.length>4){
                                        return data1.substring(0,4)+'...';
                                    }else{
                                        return data1;
                                    }
                                }else{
                                    return "";
                                }
                                break;
                        }
                    }else{
                        return "";
                    }
                }
            }
        },
        watch:{
            countDown(){},//
            patientId(){//patientId
                this.inqRightCon(2,0,this.caseId,1);//
                this.inqLeftBar(1);//
            },
        },
        filters:{
            isMarriage(type){
                let t = this,txt='';
                switch (parseInt(type)){//1-2-3-4-
                    case -1:
                        txt = '';
                        break;
                    case 1:
                        txt = '';
                        break;
                    case 2:
                        txt = '';
                        break;
                    case 3:
                        txt = '';
                        break;
                    case 4:
                        txt = '';
                        break;
                }
                return txt;
            },
            imgNum(num1,num2,num3){//
                  return parseInt(num1.length)+parseInt(num2.length)+parseInt(num3.length);
            },
            dateFilter(txt,type){//
                if(type){
                    txt = txt.split(' ');
                    return txt[0];
                }else{
                    txt = txt.split(' ');
                    txt = txt[0].split('-');
                    txt = txt[0]+''+txt[1]+''+txt[2]+'';
                    return txt;
                }

            },
            imgMore(txt,num){
                if(txt.length>num){
                    return parseInt(txt.length)-parseInt(num);
                }else{
                    return 0
                }
            },
            commOption(data){//
                let txt = '';
                if(data!=''&&!comm.isEmptyObject(data)){
                    for(let i = 0;i<data.length;i++){
                        txt+=data[i].commOptionName+'';
                    }
                    return txt.substring(0,txt.length-1);
                }else{
                    return ''
                }
            },
        },
        metaInfo: {
            title: ''
        },
        mounted() {
            let t=this;
            t.gainHeight();
            user.login({
                scene:0,
                callback(){
                    t.isBlongFn();
                }
            });
            // console.log(this.$store.state.showLoad)
        },
    }
</script>
<style lang="scss">
    @import "../../assets/scss/base.scss";
    @import "../../assets/scss/pages/caseDetail.scss";
    @import "../../utils/view-big-img/module.view-big-img.css";
</style>
